<template>
  <v-app-bar
    id="app-bar"
    app
    :dark="dark"
    :clipped-left="$vuetify.breakpoint.lgAndUp"
  >
    <v-btn
      fab
      icon
      @click="setShowDrawer(!showDrawer)"
    >
      <v-app-bar-nav-icon/>
    </v-btn>

    <v-toolbar-title>BookmarkTomb</v-toolbar-title>

    <v-spacer/>

    <v-dialog
        v-model="dialog"
        max-width="500px"
        @click:outside="resetDialog()"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          class="mx-2"
          v-bind="attrs"
          v-on="on"
          icon
        >
          <v-icon
            size="30"
            :title="$t('appBar.add')"
          >
            mdi-bookmark-plus-outline
          </v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          {{ $t('appBar.add') }}
        </v-card-title>
        <v-card-text>
          <ValidationObserver
            ref="addBM"
            v-slot="{ validated }"
          >
            <VTextFieldWithValidation
              :validated="validated"
              v-model="newBookmarkInfo.title"
              placeholder="输入书签标题"
              rules="required"
            />
            <VTextFieldWithValidation
              :validated="validated"
              v-model="newBookmarkInfo.url"
              placeholder="输入网页连接 例：https://www.baidu.com"
              rules="required"
            />
          </ValidationObserver>
          <div v-if="showOtherCollections">
            <v-row>
              <v-col cols="5">
                <v-text-field
                  value="我的收藏"
                  dense
                  outlined
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="4" style="line-height: 40px;">
                <v-btn color="blue darken-1" text @click="showOtherCollections = false">选择其他文件夹</v-btn>
              </v-col>
            </v-row>
          </div>
          <div v-else>
            <v-treeview
              :active.sync="active"
              :items="treeItems"
              activatable
              hoverable
              item-key="id"
              transition
              dense
            >
              <template v-slot:prepend="{ }">
                <v-icon>mdi-folder-outline</v-icon>
              </template>
            </v-treeview>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer/>
          <v-btn
              color="blue darken-1"
              outlined
              rounded
              width="100"
              @click="addBookmark">{{ $t('appBar.dialogAdd') }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-text-field
      class="mx-2 rounded-lg"
      style="max-width: 300px; max-height: 40px;"
      prepend-inner-icon="mdi-magnify"
      filled
      dense
      rounded
      :placeholder="$t('appBar.search')"
    >
    </v-text-field>

    <v-select
      v-model="language"
      class="mx-2"
      style=" max-width: 125px; max-height: 40px; "
      :items="languages"
      outlined
      dense
    ></v-select>

    <v-btn
      class="mx-2"
      fab
      icon
      small
    >
      <v-icon
        size="30"
        :title="$t('appBar.theme')"
      >
        mdi-tshirt-crew-outline
      </v-icon>
    </v-btn>

    <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-avatar size="40" class="mx-2" >
          <img
            :src="userAvatar"
            alt="Avatar"
            v-bind="attrs"
            v-on="on"
          >
        </v-avatar>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in personalCenter"
          :key="index"
          @click="personalCenterMethod(item.method)"
          dense
          color="success"
          :to="item.to"
        >
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>

  </v-app-bar>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
import {ValidationObserver} from "vee-validate";
import VTextFieldWithValidation from "@/views/components/VTextFieldWithValidation";

export default {
  name: 'BookmarkTombAppbar',

  components: {
    ValidationObserver,
    VTextFieldWithValidation
  },

  data: () => ({
    dialog: false,
    active: [],
    languages: [ '简体中文','English'],
    language: '简体中文',
    showOtherCollections: true,
    newBookmarkInfo: {
      parentId: 0,
      title: '',
      url: ''
    }
  }),

  props: {
    dark: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    ...mapGetters(['showDrawer', 'userAvatar', 'userCollections', 'treeItems']),
    personalCenter() {
      return [
        { title: this.$t('appBar.personalCenter.personalCenter'), to: '/myinfo', method: 'test'},
        { title: this.$t('appBar.personalCenter.managementCenter'), to: '/admin', method: 'test'},
        { title: this.$t('appBar.personalCenter.logOut'), method: 'logOut',  }
      ]
    },
    selected() {
      if (!this.active.length) return 0
      return this.active[0]
    }
  },

  async created() {
    if(localStorage.getItem('lang')){
      this.language = localStorage.getItem('lang')
    }else{
      this.language = '简体中文'
    }
    // await this.$store.dispatch('getCollections')
    // this.treeItems = generateTree(this.userCollections)
  },

  watch: {
    language() {
      if(this.language === 'English'){
        this.$i18n.locale='en'
        localStorage.setItem('lang','English')
      }else if(this.language === '简体中文'){
        this.$i18n.locale='zh'
        localStorage.setItem('lang','简体中文')
      }
    },
  },

  methods: {
    ...mapMutations({
      setShowDrawer: 'SET_SHOW_DRAWER'
    }),
    resetDialog(){
      this.newBookmarkInfo.title=''
      this.newBookmarkInfo.url=''
      this.$refs.addBM.reset()
      this.showOtherCollections = true
      this.active = []
    },
    addBookmark() {
      this.$refs.addBM.validate().then(isValidate => {
        if(isValidate){
          this.newBookmarkInfo.parentId = this.selected
          this.$store.dispatch('addBookmark',this.newBookmarkInfo).then(() => {
            // console.log(JSON.stringify(this.newBookmarkInfo))
            this.newBookmarkInfo.title = ''
            this.newBookmarkInfo.url = ''
            this.$refs.addBM.reset()
            this.dialog = false
            this.showOtherCollections = true
            this.active = []
          })
        }
      })
    },
    personalCenterMethod(method) {
      this[method]()
    },
    logOut() {
      localStorage.removeItem('Bookmark_Tomb_Token')
      this.$router.push('/login')
    },
    test() {
    }
  }
}
</script>
